<template>
	<div>
		<div class="nav-header">
			<span><img src="../../src/assets/logo.png" /></span>
		</div>
		<!--导航列表-->
		<ul class="nav-list">
			<li v-for="(item,index) in navData" v-bind:key="item.title" :class="navIndex==index? 'select':''" v-on:click="sendMsg(index)">
				<router-link :to="item.href">
					<svg class="icon" aria-hidden="true">
						<use :xlink:href="item.icon"></use>
					</svg>
					<span :style="'display:'+navBtn.display">{{item.title}}</span>
				</router-link>
				<span class="nav-item-msg" :style="'left:'+navBtn.left">{{item.title}}</span>
			</li>
		</ul>
	</div>
</template>

<script>
	export default {
		name: 'Nav',
		data: function() {
			return {
				navData: [{  //侧边菜单栏数据
					icon: "#icon-gongzuotai",
					title: "工作台",
					href: "/workbench"
				}, {
					icon: "#icon-buliding",
					title: "客户",
					href: "/customer"
				}, {
					icon: '#icon-hetong',
					title: "合同",
					href: "/constract"
				}, {
					icon: "#icon-querendakuan",
					title: "款项",
					href: "/payment"
				}, {
					icon: "#icon-note",
					title: "跟进记录",
					href: "/note"
				}, {
					icon: "#icon-chanpinpeizhi",
					title: "产品",
					href: "/product"
				}, {
					icon: "#icon-tongji",
					title: "统计分析",
					href: "/stratistics"
				}],
				navIndex: 0, //列表点击下标
			}
		},
		props: ['navBtn'],
		methods:{
			sendMsg(index){
				this.navIndex = index 
				this.$emit('func',this.navData[this.navIndex].title)
			}
		},
		mounted() {
			//判断当前页面路径是哪个.再匹配相应的导航栏选项
			for(let i = 0; i < this.navData.length; i++) {
				if(this.$route.path == this.navData[i].href) {
					this.navIndex = i;
					this.$emit('func',this.navData[this.navIndex].title)
				}
			}
		
		},
	}
</script>

<style>
	/* 导航栏头部 */
	.nav-header {
		position: absolute;
		left: 0;
		top: 30px;
		width: 100%;
		text-align: center;
	}
	
	.nav-header img {
		width: 50px;
		height: 50px;
	}
	/* 导航栏列表 */
	.nav-list {
		display: flex;
		flex-direction: column;
		width: 100%;
		text-align: center;
		margin-top: 130px;
		margin-bottom: 100px;
	}
	
	.nav-list li {
		position: relative;
		width: 100%;
		height: 50px;
		cursor: pointer;
	}
	
	.nav-list li:hover a {
		color: #fff;
	}
	
	.nav-list a {
		display: flex;
		align-items: center;
		height: 100%;
		color: #999;
		padding-left: 26px;
	}
	
	.icon {
		width: 17px;
		height: 17px;
		margin-right: 5px;
	}
	
	.nav-list .select {
		background-color: #1f282c;
	}
	
	.nav-list .select a {
		color: #fff;
	}
	
	.nav-item-msg {
		position: absolute;
		top: 12px;
		left: 182px;
		padding: 5px 10px;
		width: 70px;
		background: #222222;
		color: #fff;
		font-size: 12px;
		border-radius: 10px;
		display: none;
	}
	
	.nav-list li:hover .nav-item-msg {
		display: inline-block;
	}
</style>
