<template>
	<div class="home">
		<!--侧边导航-->
		<div id="slider-nav" :style="'width:'+navBtn.width">
			<Nav :navBtn="navBtn" @func="getTitle"></Nav>
		</div>
		<!--右边内容主体-->
		<div id="content-box" :style="'padding-left:'+navBtn.paddingLeft">
			<!--头部搜索框-->
			<header class="constant-head">
				<div class="list-btn">
					<svg class="icon" aria-hidden="true" @click="cliNavBtn">
						<use :xlink:href="navBtn.icon"></use>
					</svg>
					<span>{{title}}</span>
				</div>
				<div class="header-form">
					<label>
						<svg class="icon" aria-hidden="true" style="color: #9c9c9c;">
							<use xlink:href="#icon-Search"></use>
						</svg>
					</label>
					<input placeholder="输入客户名快速检索..." v-model="search" @blur="message = false" />
					<div v-if="message" class="tt-menu">
						<div class="tt-dataset">
							<div class="empty-message">没有找到符合条件的客户信息</div>
							<div class="tt-selectable" @click="goDetails">
								<svg class="icon" aria-hidden="true">
									<use xlink:href="#icon-buliding"></use>
								</svg>
								<span>否否发</span>
							</div>
						</div>
					</div>
				</div>
				<ul class="user-list">
					<li>
						<el-dropdown trigger="click">
							<span>
								<svg class="icon" aria-hidden="true" style="color: #0066cc;">
									<use xlink:href="#icon-dianhua"></use>
								</svg>
							</span>
							<el-dropdown-menu slot="dropdown">
								<el-card shadow="hover">
									<el-input class="number-input" v-model="number" clearable></el-input>
									<ul class="web-phone-dialpad-number-container">
										<li v-for="(item,index) of 9" :key="index" @click="number += item">{{item}}</li>
										<li @click="number += '*'">*</li>
										<li @click="number += '0'">0</li>
										<li @click="number += '#'">#</li>
									</ul>
									<div style="text-align: center;">
										<el-button type="info" icon="el-icon-phone-outline" circle style="padding: 20px;"></el-button>
									</div>
								</el-card>
							</el-dropdown-menu>
						</el-dropdown>
					</li>
					<li>
						<svg class="icon" aria-hidden="true" style="color: #0066cc;">
							<use xlink:href="#icon-xiaoxi"></use>
						</svg>
					</li>
					<li>
						<el-dropdown trigger="click">
							<span>
								<svg class="icon" aria-hidden="true">
									<use xlink:href="#icon-yonghu"></use>
								</svg>
							</span>
							<el-dropdown-menu slot="dropdown">
								<el-dropdown-item icon="el-icon-user">个人信息</el-dropdown-item>
								<el-dropdown-item icon="el-icon-setting">系统设置</el-dropdown-item>
								<el-dropdown-item icon="el-icon-question">在线帮助</el-dropdown-item>
								<el-dropdown-item icon="el-icon-download">下载手机App</el-dropdown-item>
								<el-dropdown-item icon="el-icon-service">联系客服</el-dropdown-item>
								<el-dropdown-item><hr></el-dropdown-item>
								<el-dropdown-item icon="el-icon-switch-button">退出</el-dropdown-item>
							</el-dropdown-menu>
						</el-dropdown>
					</li>
				</ul>
			</header>
			<!--子页面内容-->
			<router-view />
		</div>
	</div>
</template>

<script>
	import Nav from '../components/Nav'
	export default {
		name: 'Home',
		components: {
			Nav
		},
		data: function() {
			return {
				title: '', //字页面标题
				search: '', //搜索框内容
				login: false, //登录状态变量
				navBtn: { //点击导航收缩按钮样式
					is: false, //按钮点击状态
					width: '180px', //导航栏宽度
					display: 'inline-block', // 导航栏属性
					paddingLeft: '180px', //右边页面主体属性
					left: '182px', //悬浮提示文字的属性
					icon: "#icon-liebiaoshouqi" //导航栏按钮图标
				},
				message: false, //搜索提示框显示状态
				number: '', //号码输入框内容
			}
		},
		methods: {
			getTitle: function(data) { //获取子组件数据
				this.title = data
			},
			cliNavBtn: function() { //点击导航收缩按钮
				if (this.navBtn.is) {
					this.navBtn = {
						is: false,
						width: '180px',
						display: 'inline-block',
						paddingLeft: '180px',
						left: '182px',
						icon: "#icon-liebiaoshouqi"
					}
				} else {
					this.navBtn = {
						is: true,
						width: '70px',
						display: 'none',
						paddingLeft: '70px',
						left: '72px',
						icon: "#icon-liebiaozhankai"
					}
				}
			},
			goDetails: function() { //点击搜索结果跳转页面
				alert(this.search)
				// this.$router.push({ path:'/login' })
			},
		},
		watch: {
			search: function(v) {
				if (v == '') {
					this.message = true;
				}
			}
		},
		created: function() {
			this.login = this.$route.query.login || false;
			if (!this.login) {
				//				this.$router.push({ path:'/login' })
			}
		},

	}
</script>

<style>
	/* 侧边导航栏 */
	#slider-nav {
		position: fixed;
		left: 0;
		top: 0;
		width: 70px;
		/*width: 180px;*/
		height: 100%;
		max-height: 100%;
		background-color: #343f4b;
		overflow-x: visible;
		z-index: 20;
		transition: all .1s ease;
	}


	/* 右边主体 */
	#content-box {
		width: 100%;
		padding-left: 180px;
		transition: all .1s ease;
	}

	/* 右边头部 */
	.constant-head {
		display: flex;
		justify-content: space-between;
		align-items: center;
		height: 60px;
		width: 100%;
		background-color: #fff;
		padding: 0 20px;
		border-bottom: solid 1px #e0e1e3;
		box-sizing: border-box;
	}

	.list-btn {
		display: flex;
		justify-content: space-between;
		align-items: center;
		height: 100%;
		width: 100px;
		font-size: 16px;
		margin-right: 20px;
	}

	/* 搜索框 */
	.header-form {
		display: flex;
		align-items: center;
		position: relative;
	}

	.header-form input {
		height: 60px;
		line-height: 60px;
		background-color: transparent !important;
		border-radius: 0;
		border: 0;
		font-size: 18px;
		padding-left: 10px;
		outline: none;
	}

	.constant-head .icon {
		width: 26px;
		height: 26px;
		cursor: pointer;
	}

	.user-list {
		display: flex;
		justify-content: space-between;
		width: 160px;
	}

	/* 搜索结果提示框 */
	.tt-menu {
		position: absolute;
		top: 100%;
		left: 0px;
		z-index: 100;
		width: 100%;
		min-width: 160px;
		padding: 5px 0;
		margin: 2px 0 0;
		font-size: 1rem;
		list-style: none;
		background-color: #fff;
		background-clip: padding-box;
		border: 1px solid #ccc;
		border: 1px solid #e0e0e0;
		border-radius: .215rem;
		box-shadow: 0 3px 12px rgba(0, 0, 0, .05);
	}

	.tt-dataset {}

	.empty-message {
		display: block;
		padding: 10px 20px;
		clear: both;
		font-weight: 300;
		line-height: 1.571429;
		color: #757575;
		text-align: left;
		font-size: 16px;
	}

	.tt-selectable {
		color: #757575;
		text-decoration: none;
		cursor: pointer;
		background-color: #eee;
		outline: 0;
		display: flex;
		align-items: center;
		padding: 10px 20px;
		clear: both;
		font-weight: 300;
		line-height: 1.571429;
		text-align: left;
		font-size: 16px;
	}

	/* 电话弹框 */
	.el-dropdown-menu {
		padding: 0;
	}

	.number-input {
		height: 36px;
		line-height: 36px;
		box-sizing: border-box;
		border: 0;
		text-align: center;
		font-weight: bold;
	}

	.web-phone-dialpad-number-container {
		line-height: 1;
		width: 204px;
		margin: 10px auto 10px;
		overflow: hidden;
	}

	.web-phone-dialpad-number-container li {
		text-align: center;
		float: left;
		height: 56px;
		width: 56px;
		line-height: 56px;
		border: 1px solid #a6c1ff;
		border-radius: 100%;
		font-size: 26px;
		margin: 2px 5px;
		cursor: pointer;
		transition: all ease .2s;
	}

	.web-phone-dialpad-number-container li:hover {
		border-color: #417afc;
		color: #417afc;
	}
</style>
